@mixin header-font {
  font-family: "Roboto", serif;
  font-weight: 500;
  font-size: 12px;
}

@mixin logo-size($size) {
  width: $size;
  height: $size;
}

@mixin content-font {
  font-family: "Roboto", serif;
  font-weight: normal;
  font-size: 11px;
  color: #000;
}

@mixin display-flex-column {
  display: flex;
  flex-direction: column;
}
